/**
 * functionuseCallback
 * @param method 
 * @param wait 
 * @param immediate 
 * @returns {function(...[*]=): Promise<any>}
 */
export function debounce(method, wait, immediate) {
    let timeout;
    let debounced = function (...args) {
        let context = this;
        if (timeout) {
            clearTimeout(timeout)
        }
        if (immediate) {
            let callNow = !timeout;
            timeout = setTimeout(() => {
                timeout = null
            }, wait);
            if (callNow) {
                method.apply(context, args);
            }
        } else {
            timeout = setTimeout(() => {
                method.apply(context, args);
            }, wait)
        }
    };
    debounced.cancel = function () {
        clearTimeout(timeout);
        timeout = null
    };
    return debounced
}
